import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Platform, ActionSheetController } from 'ionic-angular';

/**
 * Generated class for the UserprofilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-userprofile',
  templateUrl: 'userprofile.html',
})
export class UserprofilePage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public platform: Platform, public actionsheetCtrl: ActionSheetController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserprofilePage');
  }


  openMenu() {
    let actionSheet = this.actionsheetCtrl.create({
      title: 'Options',
      cssClass: 'action-sheets-basic-page',
      buttons: [
        {
          text: 'Restrict',
          role: 'destructive',
          icon: !this.platform.is('ios') ? 'close' : null,
          handler: () => {
            console.log('Restrict clicked');
          }
        },
        {
          text: 'Block',
          role: 'destructive',
          icon: !this.platform.is('ios') ? 'key' : null,
          handler: () => {
            console.log('Block clicked');
          }
        },
        {
          text: 'Report',
          role: 'destructive',
          icon: !this.platform.is('ios') ? 'flag' : null,
          handler: () => {
            console.log('Report clicked');
          }
        },
        {
          text: 'About this acount',
          
          icon: !this.platform.is('ios') ? 'information' : null,
          handler: () => {
            console.log('About clicked');
          }
        },
        {
          text: 'Hide Your Story',
         
          icon: !this.platform.is('ios') ? 'eye-off' : null,
          handler: () => {
            console.log('Hide clicked');
          }
        },
        {
          text: 'Copy Profile URL',
          
          icon: !this.platform.is('ios') ? 'copy' : null,
          handler: () => {
            console.log('Copy clicked');
          }
          
        },
        {
          text: 'Share This Profile',
          
          icon: !this.platform.is('ios') ? 'share-alt' : null,
          handler: () => {
            console.log('Share clicked');
          }
        }

      ]
    });
    actionSheet.present();
  }

}



